/*  Progress*/
.time{
  display: inline-block;
  margin-top: 20px;
  color: white;
}
#seek-bar{
  position: relative;
}
#progebar{
  width: 100%;
  height: 10px;
  border-radius: 5px;
  outline: none;
  -webkit-appearance:none;
  position: relative;
  z-index: 2;
  background-color: transparent;
}
.fill{
  display: block;
  width: 0;
  background-color: blue;
  height: 10px;
  border-radius: 5px;
}
#progebar::-webkit-slider-thumb{
  -webkit-appearance:none;
  width: 20px;
  height: 20px;
  background-color: blue;
  border-radius: 50%;
  cursor: pointer;
  margin-left: -2px;
}
.bar{
  position: absolute;
  z-index: 1;
  background-color: gray;
  width: 100%;
  height: 10px;
  border-radius: 5px;
  top: 5px;
  left: 2px;
}
.img img{
  width: 200px;
  height: 200px;
  border-radius: 50%;
  -webkit-animation: snip linear 5s infinite;
          animation: snip linear 5s infinite;
  -o-object-fit: cover;
     object-fit: cover;
}
@-webkit-keyframes snip {
from{
        -webkit-transform:rotate(0deg)  ;
        -moz-transform:rotate(0deg) ;
        -o-transform:rotate(0deg) ;
}
to{
        -webkit-transform:rotate(360deg) ;
        -moz-transform:rotate(360deg) ;
        -o-transform:rotate(360deg) ;
}
}
@keyframes snip {
from{
        -webkit-transform:rotate(0deg)  ;
        -moz-transform:rotate(0deg) ;
        -o-transform:rotate(0deg) ;
}
to{
        -webkit-transform:rotate(360deg) ;
        -moz-transform:rotate(360deg) ;
        -o-transform:rotate(360deg) ;
}
}
